.userInfo {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 13px 0 8px;
    // border: 1px solid #000;
}
.userInfoName {
    @include mixin.font-setting(16px, 700, color.$gray-10);
    width: 71px;
    overflow: hidden;
    text-overflow: ellipsis;
    height: 26px;
    line-height: 26px;

    // border: 1px solid #000;
}

.userInfoAccount {
    @include mixin.font-setting(14px, 500, color.$gray-7);
    width: 71px;
    overflow: hidden;
    text-overflow: ellipsis;
    height: 22px;
    line-height: 22px;
}

